import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	computed: {
		...mapState(['gender'])
	},
	data() {
		return {
			sexChecked: true,
			tag: ['公司','家','学校'],
			activeIndex: -1,
			addressData: {
				linkman: "",
				phone: "",
				address:"",
				tower: ""
			},
			isDisabled: true
		}
	},
	onLoad() {
		this.gender == "男" ? this.sexChecked = true : this.sexChecked = false;
	},
	methods: {
		...mapMutations(['changeAddress']),
		tagChange(index) {
			this.activeIndex = index;
		},
		formSubmit(e) {
			var _this = this;
			var i = this.activeIndex;
			var d = e.detail.value;
			var thisTag;
			i == -1 ? thisTag = "" : thisTag = _this.tag[i];
			this.addressData = {
				linkman: d.linkman,
				sex: d.sex,
				phone: d.phone,
				address: d.address,
				addressType: thisTag,
				isDefault: d.default,
				tower: d.tower
			}
			this.changeAddress(this.addressData);
			uni.navigateBack();
		},
		input(e) {
			var id = e.currentTarget.id;
			var ad = this.addressData;
			var _this = this;
			for(var key in ad){
				key == id ? ad[key] = e.detail.value : ad[key];
				if(ad[key] !== ""){
					_this.isDisabled = false;
				}
				for(var k in ad){
					if(ad[k] == ""){
						_this.isDisabled = true
					}
				}
			}
		}
	}
}
